@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@100;300;400;500;600;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Bebas+Neue&family=Poppins:wght@100;300;400;500;600;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

html,body{margin:0;padding:0;font-family:"Poppins",-apple-system,BlinkMacSystemFont,"Segoe UI",Roboto,Oxygen,Ubuntu,Cantarell,"Open Sans","Helvetica Neue",sans-serif}.header{display:flex;width:100%;align-items:center;height:100px}.header a{margin:0 20px;text-decoration:none;color:#101010;font-size:12px;font-weight:600}.container{position:relative;width:1280px;min-width:1280px;margin:0 auto;display:flex;justify-content:center}.container .page{position:absolute}.container .page .inner{display:flex;justify-content:center}.container .page .inner h1{font-family:"Bebas Neue";font-size:52px;letter-spacing:.1rem}.container .page .inner h1 .line-wrap{overflow:hidden;height:66px}.container .page .inner p{font-family:"Poppins";margin-top:200px;width:340px;font-weight:300;line-height:24px;font-size:14px}.page-enter{opacity:0}.page-enter-active{opacity:1;transition:opacity 400ms;transition-delay:600ms}.page-exit{opacity:1}.page-exit-active{opacity:0;transition:opacity 400ms}
